<template>
  <div class="pem-auth-input-wraper" :class="{'pem-auth-input-required-wraper': item.required}">
    <div class="pem-auth-input-inner">
      <span class="pem-auth-input-name">{{item.name}}</span>
      <input 
        class="pem-auth-input" 
        :type="item.type" 
        :value="value" 
        @input="$emit('input', $event.target.value)" 
        :disabled="item.disabled"
        :placeholder="`请输入${item.name}`"
        :maxlength="item.maxlength">
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  props: {
    item: {
      type: Object,
      default: {
        type: 'text',
        maxlength: '500',
        required: true,
        disabled: false
      }
    },
    value: null
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
@import "~common/stylus/mixin"
  .pem-auth-input-required-wraper
    bg-image('../../common/image/minisite/auth/icon-Required')
    background-size: 0.15rem 0.15rem
    background-repeat: no-repeat
    background-position: 0.1rem center
  .pem-auth-input-wraper
    padding: 0 0.26rem 0 0.34rem
    .pem-auth-input-inner
      border-bottom: 1px solid #F3F3F3
      font-size: 0
      padding: 0.2rem 0
      .pem-auth-input-name, .pem-auth-input
        display: inline-block
      .pem-auth-input-name
        font-size: 0.32rem
        color: #202020
        padding-right: 0.6rem
      .pem-auth-input
        width: 5.5rem
        font-size: 0.28rem
        color: #202020
        &:disabled
          background-color: #fff
</style>



