<template>
  <div class="auth-entrepreneur full-screen">
    <div class="page-panel">
      <pem-scroll-view ref="scroll" class="scroll-view">
        <div>
          <div class="auth-entrepreneur-wrapper">
            <h2 class="auth-title">请填写资料</h2>
            <pem-auth-input
              v-model="item.value"
              v-for="item in fieldList" 
              :item="item"
              :key="item.code"></pem-auth-input>
          </div>
          <pem-button text="提交" @submit="submit"/>
        </div>
      </pem-scroll-view>
    </div>
    <pem-toast ref="toast" :text="toastText"></pem-toast>
  </div>
</template>

<script type="text/ecmascript-6">
import PemScrollView from 'components/scroll-view/scroll-view'
import PemAuthInput from 'components/input-auth/input-auth'
import PemButton from 'components/pem-button/pem-button'
import PemToast from 'components/pem-toast/pem-toast'
import { mapState, mapActions } from 'vuex'
import { AUTH_UPDATE } from 'store/minisite/auth'
import { MiniSiteApi } from 'api'
import { INDEX_LIST_CLEAR } from 'store/minisite/index-list'
import { setTitle } from 'common/js/util'

export default {
  data () {
    return {
      toastText: '',
      fieldList: [
      {'type': 'text', 'name': '姓名', 'code': 'name', 'required': true, 'maxlength': 10, 'check': false, 'regular': null, value: ''},
      {'type': 'text', 'name': '公司', 'code': 'corpname', 'required': true, 'maxlength': 25, 'check': false, 'regular': null, value: ''},
      {'type': 'text', 'name': '职位', 'code': 'position', 'required': true, 'maxlength': 20, 'check': false, 'regular': null, value: ''}
      ]
    }
  },
  components: {
    PemScrollView,
    PemAuthInput,
    PemButton,
    PemToast
  },
  created () {
    this._initWXShare()
    this._setTitle('项目工场-认证')
  },
  computed: mapState({auth: state => state.auth}),
  methods: {
    _initWXShare () {
      this.wxShare({
        title: '为您推荐项目工场',
        content: '我正在使用项目工场，有料的BP、有趣的VC都在这儿，一起来看项目吧！',
        linkUrl: window.location.origin
      })
    },
    _setTitle (title) {
      setTitle(title)
    },
    submit () {
      if (this._validate()) {
        this.ykAuthSave()
      }
    },
    ykAuthSave () {
      let params = {}
      for (let i = 0, len = this.fieldList.length; i < len; i++) {
        let item = this.fieldList[i]
        params[item.code] = item.value
      }
      this._ykAuthSave(params, data => {
        if (+data.resultcode === 1) {
          this.toast('提交成功')
          let options = this.auth
          options.applystatus = 200
          options.state = '已认证'
          options.type = 'syk'
          options.datatype = 50
          this.AUTH_UPDATE(options)
          this.INDEX_LIST_CLEAR()
          setTimeout(() => {
            this.$router.replace('/index')
          }, 1000)
        } else {
          let toastMsg = {
            '0': '系统异常,请联系管理员',
            '-11': '姓名为空',
            '-12': '公司为空',
            '-13': '职位为空'
          }[+data.resultcode] || '未知错误,请联系管理员'
          this.toast(toastMsg)
        }
      })
    },
    _validate () {
      for (let i = 0, len = this.fieldList.length; i < len; i++) {
        let item = this.fieldList[i]
        item.value = item.value.trim()
        if (item.required && !item.value) {
          this.toast(`请输入您的${item.name}`)
          return false
        }
        if (item.check) {
          let reg = new RegExp(item.regular)
          if (!reg.test(item.value)) {
            this.toast(`请输入正确的${item.name}`)
            return false
          }
        }
      }
      return true
    },
    toast (text) {
      this.toastText = text
      this.$refs.toast.show()
    },
    _ykAuthSave (options, callback) {
      MiniSiteApi.ykAuthSave(options).then(data => {
        callback(data)
      })
    },
    ...mapActions([
      AUTH_UPDATE,
      INDEX_LIST_CLEAR
    ])
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
@import "~common/stylus/variable"
@import "~common/stylus/mixin"
  .auth-entrepreneur-wrapper
    padding-bottom: 0.84rem
  .auth-title
    height: 0.84rem
    line-height: 0.84rem
    font-size: 0.6rem
    font-weight: bold
    color: #41403E
    padding: 0.4rem 0 0.4rem 0.3rem
  .pem-submit
    width: 6.1rem
    height: 0.88rem
    line-height: 0.88rem
    margin: 0 auto
</style>



